.service-submenu {
    position: absolute;
    text-align: start;
    list-style: none;
    top: 4.9rem;
    background-color: var(--color-text-light);
    border-radius: 12px;
    -moz-border-radius: 12px;
    -webkit-border-radius: 12px;
}
.service-submenu li {
    cursor: pointer;
    transition: 0.1s ease-in;
    border-radius: 12px;
    -moz-border-radius: 12px;
    -webkit-border-radius: 12px;
}

.service-submenu.clicked {
    display: none;
}

.submenu-item {
    display: block;
    font-weight: 600;
    width: 100%;
    height: 100%;
    padding: 16px;
    text-shadow: none;
    color: var(--color-text-dark);
}

.submenu-item a {
    display: block;
    font-weight: 600;
    width: 100%;
    height: 100%;
    color: var(--color-text-light);
    padding: 16px;
    border-bottom: none;
}
.service-submenu li:hover {
    background-color: var(--color-darkgreen-ui);
}
.service-submenu a:hover {
    color: var(--color-text-light);
}

@media (max-width:970px) {
    .service-submenu {
        position: relative;
        z-index: 1000;
        width: 100%;
        top: 1.9rem;
        text-align: center;
        background-color: var(--color-nav-dark);
        border-radius: 0;
        -moz-border-radius: 0;
        -webkit-border-radius: 0;
        cursor: pointer;
        transition: 0.5s ease-in;
    }
    
    .service-submenu.clicked {
        display: none;
    }
    
    .submenu-item {
        display: block;
        font-weight: 600;
        width: 100%;
        height: 100%;
        padding: 1.2rem;
        text-shadow: none;
        color: var(--color-text-light);
        transition: 0.5s ease-in;
    }
    
    .submenu-item a {
        display: block;
        font-weight: 600;
        width: 100%;
        height: 100%;
        color: var(--color-text-light);
        padding: 16px;
        border-bottom: none;
    }
    .service-submenu li:hover {
        background-color: var(--color-darkgreen-ui);
        padding: 12px 0;
    }
}