.wrapper{
    display: flex;
    flex-direction: row;
    width: 100%;
    max-width: 1070px;
    margin: 7rem auto;
}
.page-bodytext {
    padding-right: 50px;
    width: 63%;
}
.page-bodytext h3 {
    padding: 2rem 0 0;
    font-size: x-large;
    font-weight: 300;
}
.page-bodytext p {
    padding: 2rem 0 0;
    font-size: large;
    font-weight: 300;
}
.page-sidebar {
    background: #fff7e1;
    padding: 45px 25px;
    width: 37%;
    font-size: 16px;
    height: 100%;
}
.page-sidebar h3 {
    width: 350px;
    font-size: 30px;
    color: #f4bc25;
    font-weight: 300;
    text-align: center;
    font-family: 'Playfair Display', tahoma, arial, verdana;
}
.page-sidebar ul {
    padding-top: 2rem;
    padding-left: 2rem;
}
.page-sidebar li {
    list-style: disc;
    padding: 0.7rem 0 0;
}
.wrapper .page-bodytext a,
.wrapper .page-sidebar a {
    color: var(--color-orange);
}
@media (max-width: 1070px) {
    .wrapper{
        flex-direction: column;
        padding: 0; 
        align-items: center;
    }
    .page-bodytext{
        width: 90%;
    }
    .page-sidebar {
        width: 100%;
        max-width: 400px;
        margin-top: 5rem;
    }
    .page-sidebar h3 {
        width: 100%;
        font-size: 24px;
        line-break: anywhere;
    }
}