.headerpicture_contact {
    position: absolute;
    height: 60vh;
    width: 100%;
    object-fit: cover;
}
.otayhteyttaheadershadow {
    position: relative;
    width: 100%;
    height: 60vh;
    opacity: 0.5;
    background-color: #f2f2f2c0;
}
.otayhteyttaheadertextbox {
    position: absolute;
    top: 30%;
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: center;
}
.otayhteyttaheadertextbox > h1 {
    color: var(--color-darkgreen-logo);
    font-size: 6rem;
}
@media (max-width: 900px) {
    .otayhteyttaheadertextbox {
        padding-left: 0;
        align-items: center;
    }
    .otayhteyttaheadertextbox > h1 {
        font-size: 4rem;
    }
}
@media (max-width: 900px) {
    .otayhteyttaheadertextbox > h1 {
        font-size: 4rem;
    }
}
@media (max-width: 690px) {
    .otayhteyttaheadertextbox > h1 {
        font-size: 3rem;
    }
}
@media (max-width: 400px) {
    .otayhteyttaheadertextbox > h1 {
        font-size: 2rem;
    }
}
@media (max-width: 350px) {
    .otayhteyttaheadertextbox {
        top: 40%;
    }
}