.palveluihin_cont {
    display: flex;
    width: 100%;
    max-width: 1070;
    margin-top: 5rem;
    margin-bottom: 3rem;
}
a#linktoPalvelut {
    position: relative;
    display: flex;
    width: 50%;
    height: 150px;
    max-width: 500px;
    font-size: 18px;
    font-weight: 700;
    left: 50%;
    text-align: center;
    align-items: center;
    justify-content: center;
    color: var(--color-orange);
    background-color: #FFFFFF;
    border: 1px solid var(--color-orange);
    border-radius: 30px;
    cursor: pointer;
    transition: 100ms ease-in-out;
}
a#linktoPalvelut:hover {
    background-color: var(--color-btn-orange-hover);
}
@media (max-width: 1070px) {
    .palveluihin_cont {
        align-items: center;
        justify-content: center;
    }
    a#linktoPalvelut {
        position: static;
        max-width: 400px;
        width: 100%;
    }
}