header#Header_tietoja {
    position: relative;
    height: 80vh;
    max-height: 800px;
}
.headerpicture.tietoja {
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: cover;
}
.tietojaheadershadow {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #f2f2f21c;
    padding-top: 400px;
}
.tietojaheadershadow h1 {
    text-align: center;
    font-size: 5rem;
    color: var(--color-darkgreen-logo);
}
@media (max-height: 800px) {
    .tietojaheadershadow {
        padding-top: 40vh;
    }
}