section#Services_content2 {
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
    align-items: center;
    margin-top: 2rem;
}
section#Services_content2 .service_container {
    position: relative;
    display: flex;
    margin: 1rem auto;
    border-radius: 30px;
    width: 100%;
    max-width: 1070px;
}
.even {
    background-color: var(--color-green-ui);
}
.odd {
    background-color: var(--color-darkgreen-ui);
    color: var(--color-text-light);
}
.servicecont_img {
    height: 500px;
    max-width: 500px;
    object-fit: cover;
    max-height: 100%;
    border-radius: 30px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}
.servicecont_img.hidden {
    max-height: 400px;
}
.service_info {
    position: relative;
    display: flex;
    width: 100%;
    min-height: 400px;
    height: 100%;
    flex-direction: column;
    justify-content: space-around;
    padding: 2rem 4rem;
}
.service_info:last-child {
    align-self: center;
}
.service_info > h2{
    text-transform: capitalize;
    font-size: 28px;
    margin: 2rem 0;
}
.scardButtons{
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 350px;
    height: 100%;
    margin-top: 2rem;
}
.linktoReservation {
    color: var(--color-orange);
    width: 100%;
    padding: 0.9rem 0;
    margin-top: 15px;
    border: none;
    cursor: pointer;
    text-align: center;
    font-size: 16px;
    background-color: var(--color-text-light);
}
.prices > p {
    font-size: 18px;
    margin-top: 0.5rem;
    font-weight: bold;
}
.service_info .linktoService{
    position: relative;
    display: flex;
    width: 100%;
    height: 3rem;
    border: none;
    cursor: pointer;
    font-size: 16px;
    align-items: center;
    justify-content: center;
}
.even > .service_info .linktoService{
    background-color: var(--color-darkgreen-ui);
    color: var(--color-text-light);
}
.odd > .service_info .linktoService{
    background-color: var(--color-green-ui);
    color: var(--color-text-darkgreen);
}
@media (max-width: 1070px) {
    section#Services_content {
        justify-content: center;
        align-items: center;
    }
    .service_info {
        align-self: center;
        max-height: 400px;
    }
}
@media (max-width: 857px) {
    section#Services_content2 .service_container {
        padding: 0;
        width: 100%;
    }
    .servicecont_img {
        max-width: 350px;
        width: 100%;
        object-fit: cover;
    }
}
@media (max-width: 690px) {
    section#Services_content2 .service_container {
        padding: 0;
        width: 100%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .servicecont_img {
        width: 100%;
        max-width: 100%;
        object-fit: cover;    
        border-top-right-radius: 30px;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }
}